blockquote, body, button, caption, dd, div, dl, dt, fieldset, figure, form, h1, h2, h3, h4, h5, h6, hr, html, input, legend, li, menu, ol, p, pre, table, td, textarea, th, ul {
  margin: 0;
  padding: 0;
  outline: 0;
}

address, article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

caption, th {
  text-align: left;
}

abbr, body, fieldset, html, iframe, img {
  border: 0;
}

img {
  display: block;
}

address, cite, dfn, em, i, var {
  font-style: normal;
}

[hidefocus], summary {
  outline: 0;
}

li {
  list-style: none;
}

h1, h2, h3, h4, h5, h6, small {
  font-size: 100%;
}

sub, sup {
  font-size: 83%;
}

code, kbd, pre, samp {
  font-family: inherit;
}

q:after, q:before {
  content: none;
}

textarea {
  overflow: auto;
  resize: none;
}

label, summary {
  cursor: default;
}

a, button {
  cursor: pointer;
  color: #111;
}

a, a:hover, del, ins, s, u {
  text-decoration: none;
  outline: 0;
}

@font-face {
  font-family: "Axis";
  src: url("../font/Axis/ultralight.woff") format("woff");
}

body, button, input, keygen, legend, select, textarea {
  font-size: .75rem;
  line-height: 1.5;
  color: #333;
  outline: 0;
  font-family: Axis, KozGoPr6N, 'Heiti SC';
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

*, :after, :before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  text-size-adjust: 100%;
}

* {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html {
  -ms-touch-action: manipulation;
  touch-action: manipulation;
}

input {
  box-shadow: none;
}

video {
  width: 100%;
  height: auto;
}

::-webkit-input-placeholder {
  color: #999;
}

:-moz-placeholder {
  color: #999;
}

::-moz-placeholder {
  color: #999;
}

:-ms-input-placeholder {
  color: #999;
}

.hide {
  display: none;
}

.main-wrap {
  max-width: 1396px;
  height: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

.scroll {
  transition: all ease-in-out 0.5s;
  transform: translate(0, 20px);
  opacity: 0;
}

.is_scoped {
  transform: translate(0, 0);
  opacity: 1;
}

.page-padding-top {
  padding-top: 1.87998rem;
}

/* media start*/
header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: #fff;
  border-top: 0.26666rem solid #E50113;
  padding: .74666rem 0 .2rem;
  z-index: 90;
}

header .header-wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: .6666rem;
  line-height: .6666rem;
  background: transparent;
}

header .logo {
  width: 3.3333rem;
  height: 100%;
  background: url("../../image/Logo-fanohm-white.svg") no-repeat;
  background-size: 100%;
}

header .nav > ul li {
  display: inline-block;
  font-size: .21333rem;
  color: #000;
}

header .nav > ul li + li {
  margin-left: 1.24rem;
}

header h1 {
  height: 34px;
  line-height: 34px;
  padding: 0 .22666rem;
  font-size: .18666rem;
  border-radius: 5px;
  color: #fff;
}

footer {
  background-color: #111111;
}

footer .footer {
  position: relative;
  padding-top: .50666rem;
  padding-bottom: .50666rem;
  color: #fff;
  height: 5.454rem;
}

footer .copyright-wrap {
  position: absolute;
  bottom: .50666rem;
  color: #666;
  right: 15px;
}

@media screen and (max-width: 768px) {
  footer .copyright-wrap {
    position: relative;
    bottom: auto;
    right: auto;
  }
}

.shop-title-wrap {
  position: relative;
  text-align: center;
  margin-top: 0.8rem;
  border-top: .13333rem solid #1a1a1a;
  border-bottom: .13333rem solid #1a1a1a;
  padding: 0.93333rem 0;
  font-size: 0.37333rem;
  color: #111;
}

.shop-title-wrap h2 {
  position: relative;
  line-height: 1.178;
}

.shop-title-wrap h2:first-child:after {
  display: block;
  content: '';
  margin: 10px auto 15px auto;
  height: 3px;
  width: 30px;
  background-color: #111;
}

.shop-title-wrap p {
  line-height: 1;
  margin-top: 0.56rem;
}

.page-product1 .banner-wrap {
  height: 300px;
  width: 100%;
  background-color: #f5f5f5;
  height: 16rem;
  padding-top: 1.933333rem;
  padding-bottom: 1.34rem;
  overflow: hidden;
}

.page-product1 .banner-desc {
  white-space: nowrap;
}

.page-product1 .product-intro {
  display: inline-block;
  position: relative;
  width: 8.2rem;
  height: 2.85333rem;
}

.page-product1 .banner-desc > img {
  display: inline-block;
  height: 11.053333rem;
}

.page-product1 .product1-name {
  width: 3.02666rem;
  height: 2.85333rem;
  font-size: 2.94666rem;
  line-height: 2.94666rem;
  display: inline-block;
  vertical-align: bottom;
  position: absolute;
  bottom: .5rem;
  font-weight: 1000;
}

.page-product1 .product-desc {
  white-space: normal;
  width: 4.6rem;
  height: 2.85333rem;
  overflow: hidden;
  margin-left: .573333rem;
  display: inline-block;
  vertical-align: bottom;
  position: absolute;
  left: 3rem;
  bottom: .5rem;
}

.page-product1 .product-left :nth-child(0) {
  font-size: .26666rem;
  line-height: .846666rem;
  letter-spacing: 3px;
}

.page-product1 .product-desc :nth-child(1) {
  font-size: .22666rem;
  letter-spacing: 3px;
}

.page-product1 .banner-desc > :nth-child(2) {
  margin-left: .293333rem;
  width: 5.84rem;
}

.page-product1 .banner-desc > :nth-child(3) {
  margin-left: .66666rem;
  width: 6.90666rem;
}

.page-product1 .func-wrap {
  position: relative;
  height: 19.27999rem;
}

.page-product1 .run-img {
  width: 100%;
  height: 8rem;
}

.page-product1 .run-img img {
  width: 64%;
  position: absolute;
}

.page-product1 .rock-img {
  width: 100%;
  position: relative;
  height: 8rem;
}

.page-product1 .rock-img img {
  width: 36%;
  right: 0;
  position: absolute;
}

.page-product1 .func-desc {
  position: absolute;
  top: 4.36rem;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.page-product1 .func-desc > div {
  width: 9rem;
}

.page-product1 .func-desc > img {
  width: 9rem;
}

.page-shop .shop-img-row {
  display: flex;
}

.page-shop .shop-img-col {
  width: 50%;
}

.page-shop .shop-img-col img {
  width: 100%;
}

.page-shop .shop-img-txt {
  display: flex;
  justify-content: center;
  align-items: center;
}

.page-shop .shop-img-dl dt {
  font-size: 0.29333rem;
  color: #111;
  line-height: 33px;
}

.page-shop .shop-img-dl dd {
  font-size: 0.21333rem;
  color: #111;
  line-height: 25px;
  margin-top: 0.2rem;
}
